*, *::before, *::after {
  border: 0;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
html {
  /* 1rem = 10px */
  font-size: 62.5%;
  height: 100%;
}

body {
 padding:1.5rem 2rem;
 height: 100%;
 background-image: linear-gradient(to right bottom,rgba(61, 185, 216, 0.842), #1863a8);
 background-size: cover;
 background-position: top;
 background-repeat: no-repeat;
 background-size: cover;
}
@media screen 
and (min-device-width: 580px) 
and (-webkit-min-device-pixel-ratio: 1) { 
    body{
      padding: 5rem
    }
}

#root {
  height:100%;
  display:flex;
  flex-direction: column;
}
.onoffswitch {
  position: relative; width: 97px;
  -webkit-user-select:none; -moz-user-select:none; -ms-user-select: none;
}
.onoffswitch-checkbox {
  display: none;
}
.onoffswitch-label {
  display: block; overflow: hidden; cursor: pointer;
  border: 2px solid #999999; border-radius: 22px;
}
.onoffswitch-inner {
  display: block; width: 200%; margin-left: -100%;
  transition: margin 0.3s ease-in 0s;
}
.onoffswitch-inner:before, .onoffswitch-inner:after {
  display: block; float: left; width: 50%; height: 34px; padding: 0; line-height: 37px;
  font-size: 25px; color: white; font-family: Trebuchet, Arial, sans-serif; font-weight: bold;
  box-sizing: border-box;
}
.onoffswitch-inner:before {
  content: "F°";
  padding-left: 16px;
  background-color: #FFFFFF; color: #118CD4;
}
.onoffswitch-inner:after {
  content: "C°";
  padding-right: 16px;
  background-color: #FFFFFF; color: #118CD4;
  text-align: right;
}
.onoffswitch-switch {
  display: block; width: 31px; margin: 3px;
  background:#eeeeee;
  position: absolute; top: 0; bottom: 0;
  right: 60px;
  border: 2px solid #999999; border-radius: 22px;
  transition: all 0.3s ease-in 0s; 
}
.onoffswitch-checkbox:checked + .onoffswitch-label .onoffswitch-inner {
  margin-left: 0;
}
.onoffswitch-checkbox:checked + .onoffswitch-label .onoffswitch-switch {
  right: 0px; 
}